.App{
    
}

.info{
    width: 100%;
    height: 100vh;

    box-sizing: border-box;
    padding: 20px;

    border-left: 1px solid #dedede;

    h2{
        font-weight: bold;

        span{
            background: -webkit-linear-gradient(-70deg,#f25050,#0a66d1);
            background-clip: text;
            color: transparent;
            display: block;
            margin-top: -7px;
        }
    }

    &-input{
        &-name{
            font-size: 21px;
            margin-bottom: 6px;
            color: #333;

            span{
                vertical-align: super;
                margin-left: 6px;
                font-size: 22px;
                color: #a5a5a5;
            }
        }

        .form-group{
            padding-bottom: 10px;
            margin-bottom: 20px;
        }

        .form-group:nth-child(1){
            border-bottom: 1px solid #dedede;
        }
    }
}