.disable-padding{
    padding: 0 !important;
}

.drop{
    width: 100%;
    height: 100vh;

    overflow: auto;

    &-liquid-name{
        position: absolute;
        top: 20px;
        left: 20px;
        font-size: 80px;
        font-weight: bold;
        text-transform: uppercase;
        color: #52a9e3;
    }

    &-svg-wrapper{
        width: 100%;
        height: calc(50% + 30px);
        display: flex;
        
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
    
        position: relative;
    }

    svg{
        transform: rotate(180deg);

        *{
            
        }
    }

    &-svg{
        position: relative;
    }

    &-path{
        cursor: pointer;
        transition: all 0.3s ease;


        &:hover{
            stroke-width: 7px;
        }
    }

    &-floor{
        cursor: pointer;
    }

    &-pos-text{
        position: absolute;
        transform: translate(-50%);
        font-size: 22px;
        font-weight: bold;
    }

    &-info{
        transform: translate(-50%, 50%);
        position: absolute;

        top: calc(50% + 10px);
        left: 50%;

        font-size: 28px;
        color: rgb(145, 145, 145);
    }

    &-angle{
        stroke-dasharray: 9;

        cursor: pointer;
        transition: all 0.3s ease;


        &:hover{
            stroke-width: 7px;
        }
    }

    &-angle-text{
        position: absolute;
        bottom: 50px;
        right: 30px;

        font-weight: 600;
        font-size: 20px;
    }
}